form {
  width: 100%;
  height: 100%;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;

  &.active {
    display: flex;
  }
}

.question {
  background: #F6F6F6;;
  border-radius: 10px;
  display: none;
  flex-direction: column;
  align-items: center;
  padding: 45px 32px;
  max-width: 600px;
  width: 100%;
  box-sizing: border-box;
  margin-right: 15px;
  margin-left: 15px;
  flex-shrink: 0;

  &.active {
    display: flex;
  }
}

.question_steps {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 120%;
  text-align: center;
  color: #000000;
  margin-bottom: 12px;
}

.question_title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 120%;
  color: #000000;
  margin-bottom: 34px;
  display: inline-block;
  text-align: center;
}

.result .question_body {
  flex-direction: column;
  align-items: center;
  padding-right: 68px;
  padding-left: 68px;
}

.question_body {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  box-sizing: border-box;
  padding-right: 60px;
  padding-left: 60px;
}

.question_checkbox {
  display: none;
}

.question_checkbox:checked + .question_answer {
  background: #2B4FA9;
  color: white;
}

.question_answer {
  flex-basis: calc(50% - 8px);
  flex-grow: 0;
  flex-shrink: 0;
  flex-shrink: 0;
  background: #E7E7E7;
  border-radius: 5px;
  padding: 17px 0;
  margin-bottom: 16px;
  box-sizing: border-box;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: normal;
  line-height: 120%;

  &:hover {
    cursor: pointer;
  }

  &:nth-of-type(odd) {
    margin-right: 16px;
  }
}

.question_text-input {
  background: #E7E7E7;
  border-radius: 5px;
  border: none;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120%;
  color: black;
  width: 100%;
  padding: 17px 20px;
  margin-bottom: 10px;
  box-sizing: border-box;
  width: 400px;

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    appearance: none;
    margin: 0;
  }

  &[type=number] {
    -moz-appearance: textfield;
  }

  &.valid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.646 14.818a.5.5 0 00.707 0l8.132-8.132A1 1 0 1119.9 8.1l-9.193 9.193a1 1 0 01-1.414 0l-4.95-4.95a1 1 0 111.414-1.414l3.89 3.89z' fill='%2315AF40'/%3E%3C/svg%3E") 98% center no-repeat, #E7E7E7;
  }

  &.invalid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17 7L7 17' stroke='%23DC5858' stroke-width='2' stroke-linecap='round'/%3E%3Cpath d='M17 17L7 7' stroke='%23DC5858' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E%0A") 98% center no-repeat, #E7E7E7;
  }

  &::placeholder {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 120%;
    color: #939393;
  }
}

.question_prompt {
  font-size: 15px;
  color: #929292;
}

.question_attention {
  display: inline-block;
  width: 100%;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 120%;
  text-align: center;
  color: #000000;
  margin-top: 15px;
  margin-bottom: 18px;
}

.question_protection {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 120%;
  color: #BD4D4E;
  margin-bottom: 17px;
  display: flex;
  flex-direction: row;
  align-items: center;

  & svg {
    margin-right: 12px;
  }
}

#accepting {
  display: none;
}

#accepting:checked ~ .question_accepting::before {
  background: url("data:image/svg+xml,%0A%3Csvg width='10' height='8' viewBox='0 0 10 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.799805 3.99971L4.53314 6.79971L9.19981 1.19971' stroke='%232B4FA9'/%3E%3C/svg%3E%0A") center no-repeat;
}

.question_accepting {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 120%;
  text-align: center;
  color: #2B4FA9;
  position: relative;
  display: inline-block;
  padding-left: 18px;
  margin-bottom: 26px;

  &:hover {
    cursor: pointer;
  }

  &::before {
    content: "";
    display: inline-block;
    width: 8px;
    height: 8px;
    border: 1px solid #2B4FA9;
    background: none;
    position: absolute;
    top: calc(50% - 4px);
    left: 0;
  }

  & a {
    color: #2B4FA9;
  }
}

.first-question .question_pagination {
  justify-content: flex-end;
}

.result .question_pagination {
  justify-content: center;
  margin-top: 0;
  padding-right: 68px;
  padding-left: 68px;
}

.question_pagination {
  margin-top: 34px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
  padding-right: 60px;
  padding-left: 60px;

  &--smaller-margin {
    margin-top: calc(34px - 16px);
  }

  &--small-margin {
    margin-top: calc(34px - 10px);
  }
}

.question_next {
  background: #15AF40;
  box-shadow: 0px 0px 50px rgba(21, 175, 64, 0.6);
  border-radius: 5px;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 120%;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  padding: 17px 0;
  flex-basis: 160px;
  border: none;

  &.submit {
    text-transform: none;
  }

  &.disabled {
    opacity: 0.3;
    pointer-events: none;

    &:hover {
      cursor: auto;
    }
  }

  &:hover {
    cursor: pointer;
  }

  &.submit {
    flex-basis: 280px;
    font-size: 18px;
    font-weight: 700;
  }
}

.question_prev {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 120%;
  color: #2B4FA9;
  margin-bottom: 1px;
  border-bottom: 1px solid #2B4FA9;

  &:hover {
    cursor: pointer;
  }
}

.valid-error {
  color: #CB3D54;
  font-size: 11px;
  text-align: center;
  width: 100%;
  display: inline-block;
}

@media(max-width: 767px) {
  .form {
    justify-content: flex-start;
    padding-top: 90px;
    padding-bottom: 90px;
  }

  .question {
    padding: 24px 15px;
    width: calc(100% - 30px);
  }

  .question_steps {
    font-size: 14px;
    line-height: 120%;
    margin-bottom: 8px;
  }

  .question_title {
    font-size: 18px;
    line-height: 120%;
    margin-bottom: 24px;
  }

  .question_body {
    flex-direction: column;
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
    box-sizing: border-box;
  }

  .first-question .question_body,
  .question_body.without-padding {
    padding: 0;
  }

  .result .question_body {
    padding: 0;
  }

  .question_answer {
    flex-basis: auto;
    width: 100%;
    margin-bottom: 10px;

    &:nth-of-type(odd) {
      margin-right: 0;
    }
  }

  .question_pagination {
    margin-top: 14px;
    padding-left: 10px;
    padding-right: 0;

    &--smaller-margin {
      margin-top: 14px;
    }
  }

  .result .question_pagination {
    padding-left: 0;
    padding-right: 0;
  }

  .first-question .question_pagination {
    margin-top: 80px;
    justify-content: flex-end;
  }

  .question_next {
    flex-basis: 170px;
  }

  .result .question_next {
    width: 100%;
    box-sizing: border-box;
    flex-grow: 1;
  }

  .question_next {
    font-size: 16px;
  }

  .question_prev {
    font-size: 16px;
  }

  .question_text-input {
    margin-bottom: 10px;
    width: 100%;
    font-size: 16px;

    &::placeholder {
      font-size: 16px;
    }
  }

  .question_attention {
    font-size: 11px;
    margin-top: 6px;
    margin-bottom: 22px;
  }

  .question_accepting {
    margin-bottom: 20px;
  }

  .question_protection {
    margin-bottom: 12px;
  }
}

.loader {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: rgba(0,0,0,.3);
  z-index: -99;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;

  &::before {
    content: url(../img/loader.svg);
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  &.active {
    z-index: 99;
    opacity: 1;
    pointer-events: all;
    visibility: visible;
  }
}

.disable_page {
  background: white;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: calc(100% - 30px);
  height: 100%;
  z-index: 99999999;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-right: 15px;
  padding-left: 15px;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-size: 28px;
  font-weight: bold;
  line-height: 120%;

  & span {
    font-weight: normal;
    margin-top: 5px;
  }

  &.active {
    display: flex;
  }
}

@media(max-width: 767px) {
  .disable_page {
    font-size: 18px;
  }
}